<template lang="pug">
    section(class="about")
        div(class="container")
            div(class="title")
                h2 {{ AboutData.choose_us.title }}
            div(class="about-content")
                .about-text
                    p {{ AboutData.choose_us.body }}
</template>

<script>
import { mapState } from "vuex";

export default {
    name: "WhyChoose",
    metaInfo() {
        return {
            title: process.env.VUE_APP_TITLE,
            titleTemplate: `%s | ${this.AboutData.website_title}`,
            meta: [
                { charset: "utf-8" },
                {
                    name: "description",
                    content: this.AboutData.meta_description,
                },
                {
                    name: "keywords",
                    content: this.AboutData.meta_keywords,
                },
            ],
        };
    },
    mounted() {
        this.$store.dispatch("getAboutData");
    },
    computed: {
        ...mapState(["AboutData"]),
    },
};
</script>
